import contact from "@/components/contact/contact.vue";
export default {
	components:{
		contact
	},
	data() {
		return {
			title: 'Hello',
			check:false,
			phone:""
		}
	},
	onLoad(option) {
		console.log(option);
		if(option.phone){
			this.phone = option.phone;
			console.log(this.phone);
		}else{
			uni.showModal({
				showCancel:false,
				title:"未获取到手机号码",
				content:"按确定后，将返回生成页。",
				success:function(res){
					if(res.confirm){
						uni.navigateBack({
							delta:2
						})
					}
				}
			})
		}
	},
	methods: {
		buy(){
			if(this.check){
				uni.navigateBack({
					delta:2
				})
			}else{
				uni.navigateTo({
					"url":"/pages/phone/buyphone?phone="+this.phone
				})
			}
		}
		
	},
	watch:{
		
	}
}
